import * as AT from "./actionTypes";
const defaultState = {
  ingredients: null,
  price: 2,
  purchasable: false,
  burgers: [],
  totalprice: 0
};

const PRICES = {
  meat: 2,
  bacon: 2.5,
  cheese: 1.5,
  salad: 0.5
};

const countValues = obj => {
  let count = 0;
  Object.keys(obj).map(ig => {
    count += obj[ig];
    return ig;
  });
  return count;
};

const reducer = (state = { ...defaultState }, action) => {
  switch (action.type) {
    default:
      return state;
    case AT.INITIALIZE:
      return {
        ...state,
        ingredients: {
          ...action.payload
        }
      };
    case AT.ADD_INGREDIENT:
      return {
        ...state,
        ingredients: {
          ...state.ingredients,
          [action.payload]: state.ingredients[action.payload] + 1
        },
        price: state.price + PRICES[action.payload],
        purchasable: true
      };
    case AT.REM_INGREDIENT:
      if (!state.ingredients[action.payload]) return { ...state };

      return {
        ...state,
        ingredients: {
          ...state.ingredients,
          [action.payload]: state.ingredients[action.payload] - 1
        },
        price: state.price - PRICES[action.payload],
        purchasable: !(countValues(state.ingredients) <= 1)
      };
    case AT.CHECKEDOUT:
      return {
        ...state,
        burgers: [...state.burgers, state.ingredients],
        totalprice: state.price + state.totalprice,
        ingredients: defaultState.ingredients,
        price: defaultState.price
      };
    case AT.PLACEORDER:
      return {
        ...defaultState
      };
  }
};

export default reducer;
