import Axios from "../axios-database";

export const ADD_INGREDIENT = "ADD_INGREDIENT";
export const REM_INGREDIENT = "REM_INGREDIENT";
export const CHECKEDOUT = "CHECKEDOUT";
export const PLACEORDER = "PLACEORDER";
export const INITIALIZE = "INITIALIZE";

export const addIngredient = ingType => {
  return {
    type: ADD_INGREDIENT,
    payload: ingType
  };
};
export const remIngredient = ingType => {
  return {
    type: REM_INGREDIENT,
    payload: ingType
  };
};
export const checkedout = () => {
  return {
    type: CHECKEDOUT
  };
};
export const placeOrder = () => {
  return {
    type: PLACEORDER
  };
};
export const initialize = data => {
  return {
    type: INITIALIZE,
    payload: data
  };
};
export const initializeASYNC = () => {
  return dispatch => {
    let downloadedData = null;
    Axios.get("/ingredients.json")
      .then(response => dispatch(initialize(response.data)))
      .catch(error => {
        console.log("error occured while fetching ingredients");
      });
    dispatch(initialize(downloadedData));
  };
};
