import React from "react";
import classes from "./SideDrawer.module.css";
import NavigationItems from "../../NavigationItems/NavigationItems";
import Logo from "../../../logo.png";
import Backdrop from "../Backdrop/Backdrop";
const sideDrawer = props => {
  let localClass = [
    classes.sideDrawer,
    props.issideDrawerOpen ? classes.Open : classes.Close
  ];
  return (
    <React.Fragment>
      <Backdrop
        show={props.issideDrawerOpen}
        clickListener={props.clickListener}
      />

      <div className={localClass.join(" ")}>
        <img src={Logo} onClick={props.clickListener} alt={"MENU"} />
        <nav>
          <NavigationItems />
        </nav>
      </div>

      <button
        className={classes.sideDrawerOpener}
        onClick={props.openSideDrawer}
      >
        <img src={Logo} alt={"MENU"} />
      </button>
    </React.Fragment>
  );
};
export default sideDrawer;
